import { Button, Input, message, Popconfirm } from 'antd';
import moment from 'moment';
import React, { FC, useContext, useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { NEW_FEATURES } from '../../../../../../feature-toggle';
import { IPayment } from '../../../../interfaces';
import { setPromoDiscount, userContext, workspaceContext } from '../../../../state';
import { openWebsitePricing } from '../../../../utils/open-site';
import { requestUser, requestWorkspace, sendActionAnalytics } from '../../../common/api';
import LoadingPage from '../../../common/loading-page';
import { requestPromoDiscount } from '../../../pricing/api';
import { IPromoDiscount, IPromoGifts } from '../../../pricing/interfaces';
import {
  deleteBillingCard,
  deleteBillingPayPal,
  deleteSigmaBillingCard,
  requestPayments,
  requestPaymentsWorkspace,
  sendPromo,
} from '../../api';
import { TabWrapper } from '../../menu/tab-wrapper';
import { PageTitle } from '../../styles';
import {
  BlockInformationPay,
  ButtonChange,
  Hr,
  NamePaymentMethod,
  Row,
  Subtitle,
  TextEmail,
  TextGhostPayment,
  TextNameTarif,
} from '../domElements/billing';
import PaymentsTable from '../payments-table';
import PromoDiscountView from '../promo/discount-data'
import PromoActivatedGifts from '../promo/gifts-data';
import { ActivatedGiftsContainer } from '../promo/styles';
import { getEmail } from '../../../../ui/Footer/api';

const BillingPersonalAreaPage: FC = () => {
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [payments, setPayments] = useState<IPayment[]>([]);
  const [promo, setPromo] = useState<string>('');
  const [isPromoLoading, setIsPromoLoading] = useState<boolean>(false);
  const [promoGiftsData, setPromoGiftsData] = useState<IPromoGifts>({} as IPromoGifts);
  const [promoDiscountData, setPromoDiscountData] = useState<IPromoDiscount>(
    {
      discount: {
        active: false,
        discountType: 'percent',
        value: 0,
      },
    },
  );

  const workspace = useContext(workspaceContext);

  const {
    email,
    profiles: userProfiles,
    plan: userPlan,
    card = {},
    paypalSubscriptionId,
    isQuickSettingsEnabled,
    updateUser,
  } = useContext(userContext);

  const {
    id: workspaceId,
    planId: workspacePlanId,
    planName: workspacePlanName,
    profilesCount: workspaceProfilesCount,
    planProfilesMax: workspaceMaxProfiles,
    updateWorkspace,
  } = useContext(workspaceContext);

  const { t: translation } = useTranslation();

  const planName = NEW_FEATURES.workspaces ? workspacePlanName : userPlan.name;
  const profilesCount = NEW_FEATURES.workspaces ? workspaceProfilesCount : userProfiles;
  const planProfilesMax = NEW_FEATURES.workspaces ? workspaceMaxProfiles : userPlan.maxProfiles;
  const planStr = `${planName} (${profilesCount}/${planProfilesMax} ${translation('personalArea.billing.profile', { count: planProfilesMax })})`;

  const loadPayments = async (): Promise<void> => {
    let newPayments: IPayment[];
    if (NEW_FEATURES.workspaces) {
      newPayments = await requestPaymentsWorkspace(workspaceId);
    } else {
      newPayments = await requestPayments();
    }

    newPayments.sort((curr, next) => {
      const firstDate = moment(curr.expiresIn).utc();
      const secondDate = moment(next.expiresIn).utc();

      return firstDate < secondDate ? 1 : -1;
    });

    setPayments(newPayments);
    const { card: userCard = {}, subscription = {} } = await requestUser().catch(() => ({}));
    updateUser({ card: userCard, subscription });
  };

  useEffect(() => {
    if (!email) {
      return;
    }

    loadPayments().then(() => setIsLoading(false));
  }, [email, isQuickSettingsEnabled, workspaceId, workspacePlanId, NEW_FEATURES.workspaces]);

  const showPaymentHistory = (): JSX.Element|null => {
    if (!card.brand && card.type === 'sigma_card') {
      card.brand = 'Card';
    }

    const hasPaymentMethod = card.brand || paypalSubscriptionId;
    if (!hasPaymentMethod) {
      return null;
    }

    return (
      <>
        <TextGhostPayment>
          {translation('personalArea.billing.paymentMethodColumn')}
        </TextGhostPayment>
        {showPaymentCard()}
        {showPaymentPaypal()}
        <Hr style={{ marginTop: 15 }} />
      </>
    );
  };

  const ucFirst = (str = 'Ð¡ard') => str[0].toUpperCase() + str.slice(1);

  const showPaymentCard = (): JSX.Element|null => {
    if (!card.brand) {
      return null;
    }

    const deleteCardWay = card.type === 'sigma_card' ? deleteSigmaBillingCard : deleteBillingCard;

    return (
      <>
        <Row style={{ marginTop: 30 }}>
          <BlockInformationPay>
            <NamePaymentMethod>
              {ucFirst(card.brand)}
            </NamePaymentMethod>
            <TextEmail>
              {card.lastNumbers}
            </TextEmail>
          </BlockInformationPay>
          <Popconfirm
            placement='top'
            title={translation('personalArea.billing.popConfirmDelete')}
            onConfirm={(): Promise<void> => deleteCardWay().then(() => updateUser({ card: {} }))}
            okText={translation('base.yes')}
            cancelText={translation('base.no')}
          >
            <ButtonChange style={{ width: 100, marginLeft: 200, marginTop: 2 }}>
              {translation('personalArea.billing.deleteSubmitBtnText')}
            </ButtonChange>
          </Popconfirm>
        </Row>
      </>
    );
  };

  const showPaymentPaypal = (): JSX.Element|null => {
    if (!paypalSubscriptionId) {
      return null;
    }

    return (
      <Row style={{ marginTop: 30 }}>
        <BlockInformationPay>
          <NamePaymentMethod>
            PayPal
          </NamePaymentMethod>
          <TextEmail>
            {paypalSubscriptionId}
          </TextEmail>
        </BlockInformationPay>
        <Popconfirm
          placement='top'
          title={translation('personalArea.billing.popConfirmDelete')}
          onConfirm={(): Promise<void> => deleteBillingPayPal().then(() => updateUser({ paypalSubscriptionId: '' }))}
          okText={translation('base.yes')}
          cancelText={translation('base.no')}
        >
          <ButtonChange style={{ width: 100, marginLeft: 114, marginTop: 2 }}>
            {translation('personalArea.billing.deleteSubmitBtnText')}
          </ButtonChange>
        </Popconfirm>
      </Row>
    );
  };

  const changPlanClick = (e: any) => {
    sendActionAnalytics('clicked buy plan', { actionInfo: 'billing plans' });
    e.preventDefault();
    openWebsitePricing({
      workspaceId,
      isQuickSettingsEnabled,
      isShowPaymentMethods: true,
    });
  };

  useEffect(() => {
    requestPromoDiscount({ workspaceId: workspace.id }).then(setPromoDiscountData);
  }, [workspace.id]);

  const onPromoInsert = (event: React.ChangeEvent<HTMLInputElement>): void => {
    setPromo(event.target.value.toUpperCase());
  };

  const checkPromo = async (): Promise<void> => {
    setPromoGiftsData({} as IPromoGifts);
    setIsPromoLoading(true);
    try {
      const result = await sendPromo(promo, workspaceId).finally(() => {
        setPromo('');
        setIsPromoLoading(false);
      });

      const { discount: promoDiscount } = result;
      setPromoDiscount(promoDiscount);

      message.success(translation('personalArea.billing.alertPromoCodeActivated'));
      setPromoGiftsData(result);

      if (result.period.amount) {
        if (NEW_FEATURES.workspaces) {
          const { planExpiresAt } = await requestWorkspace(workspaceId);
          updateWorkspace({ planExpiresAt });
        } else {
          const { planExpireDate } = await requestUser();
          updateUser({ planExpireDate });
        }
      }
    } finally {
      setPromo('');
      setIsPromoLoading(false);
      setIsLoading(false);
    }
  };

  const sortPaymentsByCreatedDateDesc = (prev: IPayment, next: IPayment): number => {
    if (prev.createdAt > next.createdAt) {
      return -1;
    }

    if (prev.createdAt < next.createdAt) {
      return 1;
    }

    return 0;
  };

  const sortedPayments = useMemo(() => [...payments.sort(sortPaymentsByCreatedDateDesc)], [payments]);

  if (isLoading) {
    return (
      <TabWrapper tabKey='billing'>
        <PageTitle>
          {translation('personalArea.billing.title')}
        </PageTitle>
        <LoadingPage />
      </TabWrapper>
    );
  }

  return (
    <TabWrapper tabKey='billing'>
      <PageTitle>
        {translation('personalArea.billing.title')}
      </PageTitle>
      <Row style={{ marginTop: 22 }}>
        <TextNameTarif>
          {planStr}
        </TextNameTarif>
        <ButtonChange onClick={changPlanClick}>
          {translation('personalArea.billing.changePlanSubmitBtnText')}
        </ButtonChange>
      </Row>
      <Hr style={{ marginTop: 11 }} />
      {showPaymentHistory()}
      <Subtitle style={{ paddingTop: 30 }}>
        {translation('personalArea.billing.subTitlePaymentHistory')}
      </Subtitle>
      <div>
        <PaymentsTable
          payments={sortedPayments}
          isQuickSettingsEnabled={isQuickSettingsEnabled}
        />
      </div>
      <Subtitle>
        {translation('personalArea.billing.CouponSubmitBtnText')}
      </Subtitle>
      <Row
        style={{
          alignItems: 'center',
          marginTop: 20,
        }}
      >
        <Input
          style={{
            width: 202,
          }}
          value={promo}
          onChange={onPromoInsert}
          placeholder={translation('personalArea.billing.placeholderInputEnterCode') || ''}
        />
        <Button
          disabled={!promo}
          loading={isPromoLoading}
          onClick={checkPromo}
          type='default'
          style={{
            backgroundColor: 'var(--F7F7F8-personal-area-billing)',
            marginLeft: 8,
          }}
        >
          {translation('personalArea.billing.applySubmitBtnText')}
        </Button>
      </Row>
      <ActivatedGiftsContainer>
        <PromoDiscountView discountData={promoDiscountData.discount} />
      </ActivatedGiftsContainer>
      <ActivatedGiftsContainer>
        <PromoActivatedGifts promoGiftsData={promoGiftsData} />
      </ActivatedGiftsContainer>
    </TabWrapper>
  );
};

export default BillingPersonalAreaPage;
